ul{
    list-style: none;
    margin: 0;
    padding: 0;
 }
.wrapper{
    width: 500px;
    margin: 200px 350px;
    position: relative;
    background-color: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}
.wrapper .header{
    height: 50px;

}
.wrapper .header h1{
     margin: 0;
     color: pink;
     font-size: 100px;
     position: absolute;
     top:-125px;
}
.wrapper .header input{
    width: 500px;
    height: 100%;
    padding: 5px 70px;
    box-sizing: border-box;
    font-size: 30px;
    border: 1px solid #ccc;
    background-color: rgba(0, 0, 0, 0.003);
    box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
}
.wrapper .header input::-webkit-input-placeholder{
    color:#ccc;
} 
.wrapper .main {
    position: relative;
}
.wrapper .main label:nth-child(1){
    color: #999;
    font-weight: bold;
    position: absolute;
    top: -35px;
    left: 23px
}
.wrapper .main ul li{
    position: relative;
    height: 50px;
    border-bottom: 1px solid #ccc;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
}
 #checkStyle{
    width: 20px;
    height: 20px;
    border: 0;
    margin: 10px 25px;
    cursor: pointer;
    background-color: rgba(240, 84, 77, 1);
}
.wrapper .main ul li .toggle{
    color: #000;
    font-size: 25px;
    border: none;
}
.wrapper .main ul li ._toggle{
    color: #ccc;
    font-size: 25px;
    text-decoration: line-through;
    border: none;
}
.wrapper .main ul li .del{
    position: absolute;
    right: 0;
    width: 40px;
    height: 40px;
    border: 0;
    font-weight: 400;
    box-sizing: border-box;
    font-size: 24px;
    color: #fff;
    background: none;
}
.wrapper .main ul li .del::after{
    content:  'Ã—';
}
.wrapper .main ul li .del:hover{
    color: red;
    cursor: pointer;
}
.wrapper .footer{
    position: relative;
    height: 50px;
    padding: 5px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    background-color: #fff;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2),
                0 8px 0 -3px #fff,
                0 9px 1px -3px rgba(0, 0, 0, 0.2),
                0 16px 0 -6px #fff,
                0 17px 0 -6px  rgba(0, 0, 0, 0.2);
}
.wrapper .footer .filter{
    position: absolute;
    right: 30%;
    bottom: 30%;
}
.wrapper .footer .filter div{
    padding: 3px ;
    margin-left: 12px; 
    display: inline-block;
}
.wrapper .footer .filter div:hover{
    border: 1px solid #ccc;
    cursor: pointer;
}
.wrapper .footer .filter .active{
    border: 1px solid #ccc;

}
.wrapper .footer .clear{
    position: absolute;
    right: 3px;
    bottom: 30%;
    border: none;
    background-color: #fff;
    font-size: 14px;
}
.wrapper .footer .clear:hover{
    cursor: pointer;
    border: 1px solid #ccc;

}